@font-face {
  font-family: "Montserrat";
  src: local("montserrat-medium"), url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-regular"), url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-semibold"), url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-extrabold"), url("../fonts/montserrat-extrabold.woff2") format("woff2"), url("../fonts/montserrat-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.page {
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #242222;
}

.container {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 9px;
}

.header {
  flex: 0 0 auto;
  min-height: 31px;
}

.header__inner {
  display: flex;
  align-items: center;
  height: 100%;
}
.header__inner svg {
  margin-right: 4px;
}
.header__inner a {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 8px;
  line-height: 10px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-right: 9px;
}

.header__contact {
  display: flex;
  margin-left: auto;
  margin-right: 2px;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.intro h1,
.intro p {
  margin: 0;
}
.intro h1 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 61px;
  line-height: 51px;
  font-weight: 800;
  font-style: normal;
  background: linear-gradient(0deg, #c7e80a 0%, #ffffff 100%);
  text-transform: uppercase;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  padding-bottom: 5px;
}
.intro p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: -0.1px;
  text-align: center;
}

.intro__wrapper {
  padding: 20px 0;
  position: relative;
  min-height: 515px;
}

.intro__offer {
  margin-top: 33px;
}
.intro__offer p {
  color: #c7e80a;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0;
  padding-bottom: 2px;
  text-align: left;
}
.intro__offer span {
  border-bottom: 1px solid #c7e80a;
}

.intro__list {
  padding-left: 15px;
  margin-top: 37px;
}

.intro__item {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-bottom: 11px;
  letter-spacing: 0.15px;
}
.intro__item span {
  border-bottom: 1px solid #ffffff;
}

.intro__address {
  width: 134px;
  margin-left: 3px;
}
.intro__address p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 9px;
  line-height: 10px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  padding-bottom: 5px;
  text-align: center;
}
.intro__address svg {
  vertical-align: sub;
}

.intro__img {
  position: absolute;
  top: 287px;
  right: 9px;
  z-index: -1;
}
.intro__img::before {
  content: "";
  background-image: url("../img/intro/intro-backlight.svg");
  background-repeat: no-repeat;
  width: 545px;
  height: 594px;
  position: absolute;
  left: -224px;
  top: -150px;
  z-index: -1;
}
.intro__img::after {
  content: "";
  background-image: url("../img/intro/intro-lightning-bolts.svg");
  position: absolute;
  width: 270px;
  height: 247px;
  top: 2px;
  left: -88px;
}

.about {
  border-radius: 50% 50% 0% 0%/7% 7% 85% 85%;
  background: linear-gradient(0deg, #242222 0, #432545 100%);
  min-height: 447px;
}

.about__desc {
  padding: 53px 0 17px;
  letter-spacing: 0.1px;
}
.about__desc p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  font-style: normal;
  margin: 0;
  color: #ffffff;
}
.about__desc span {
  color: #c7e80a;
}

.about__work-schedule {
  margin-bottom: 48px;
}
.about__work-schedule p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  font-style: normal;
  margin: 0;
  color: #c7e80a;
  padding-bottom: 3px;
}

.about__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.about__list-item {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  font-style: normal;
  color: #ffffff;
}

.about__more {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
  margin: 0;
  padding-bottom: 20px;
}
.about__more a {
  color: #c7e80a;
}

.footer {
  flex: 0 0 auto;
  padding-bottom: 45px;
}

.footer__wrapper {
  min-height: 287px;
  background-color: #432545;
  border-radius: 20px;
  padding: 29px 33px 22px;
}
.footer__wrapper a,
.footer__wrapper p,
.footer__wrapper h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}
.footer__wrapper h2 {
  font-weight: 700;
  margin: 0;
  text-align: center;
  padding-bottom: 23px;
}

.footer__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-top: 8px;
}

.footer__contacts-item {
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
}
.footer__contacts-item:last-child {
  padding-bottom: 0;
}
.footer__contacts-item p {
  margin: 0;
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-top: 18px;
}
.footer__social-list li {
  margin-right: 15px;
}

.button-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.button-link--intro {
  min-height: 21px;
  border: 1px solid #c7e80a;
  background-color: transparent;
  color: #c7e80a;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  line-height: 12px;
  font-weight: 500;
  font-style: normal;
}
.button-link--about {
  max-width: 611px;
  background-color: #c7e80a;
  min-height: 50px;
  color: #242222;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-style: normal;
  border-radius: 30px;
  margin-bottom: 35px;
}